<script>
import { generate as generateNewSentence } from 'services/loadingText.service';
import { getRandomInt } from 'services/utilities.service';
import VariableTimer from 'services/variableTimer.service';
let vt = {};

export default {
    name: 'loading',
    data: function() {
        return {
            loadingText: 'Loading'
        }
    },
    created() {
        vt = new VariableTimer(() => {
            const newInterval = getRandomInt(25, 1500);

            this.loadingText = generateNewSentence();

            return newInterval;
        }, 1000);

        vt.start();
    },
    beforeDestroy() {
        vt.stop();
    }
}
